import React, { useState } from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  min-height: 100vh;
  background: #f4f6fa;
`;
const Nav = styled.nav`
  background: #fff;
  padding: 20px 32px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  display: flex;
  align-items: center;
  justify-content: space-between;
`;
const Logo = styled.div`
  font-size: 22px;
  font-weight: 700;
  color: #0078ff;
`;
const Content = styled.div`
  margin: 60px auto 0 auto;
  max-width: 600px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
  padding: 40px 32px;
  text-align: center;
`;

const UploadButton = styled.button`
  background-color: #0078ff;
  color: white;
  padding: 14px 28px;
  border: none;
  border-radius: 4px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  margin-top: 30px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
  &:hover {
    background-color: #0066cc;
    transform: translateY(-2px);
    transition: all 0.2s;
  }
`;

const ButtonContainer = styled.div`
  margin-top: 40px;
`;

const FileInput = styled.input`
  display: none;
`;

const StatusMessage = styled.div`
  margin-top: 20px;
  padding: 10px;
  border-radius: 4px;
  background-color: ${props => props.success ? '#e6f7ea' : '#ffebee'};
  color: ${props => props.success ? '#1e8e3e' : '#d32f2f'};
`;

function MainPage() {
  const [status, setStatus] = useState(null);
  const [loading, setLoading] = useState(false);
  const fileInputRef = React.useRef(null);

  const handleUploadClick = () => {
    fileInputRef.current.click();
  };

  const handleFileChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const fileExtension = file.name.split('.').pop().toLowerCase();
    if (fileExtension !== 'xlsx' && fileExtension !== 'xls') {
      setStatus({ success: false, message: 'Excel 파일만 업로드 가능합니다.' });
      return;
    }

    const formData = new FormData();
    formData.append('file', file);

    setLoading(true);
    setStatus(null);

    try {
      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData,
      });

      const result = await response.json();
      
      if (response.ok) {
        setStatus({ success: true, message: '파일이 성공적으로 업로드됐습니다.' });
      } else {
        setStatus({ success: false, message: result.message || '업로드 중 오류가 발생했습니다.' });
      }
    } catch (error) {
      setStatus({ success: false, message: '서버 연결 중 오류가 발생했습니다.' });
    } finally {
      setLoading(false);
      e.target.value = null; // 파일 입력 초기화
    }
  };

  console.log("MainPage 렌더링 중...");

  return (
    <Wrapper>
      <Nav>
        <Logo>coupang wing</Logo>
        <div>판매자센터</div>
      </Nav>
      <Content>
        <h2>환영합니다!</h2>
        <p>쿠팡 판매자센터 메인 페이지입니다.<br />
        필요한 기능을 차례로 추가해보세요.</p>
        
        <FileInput
          ref={fileInputRef}
          type="file"
          accept=".xlsx, .xls"
          onChange={handleFileChange}
        />
        
        <ButtonContainer>
          <UploadButton onClick={handleUploadClick} disabled={loading}>
            {loading ? '처리 중...' : '엑셀 파일 업로드'}
          </UploadButton>
        </ButtonContainer>
        
        {status && (
          <StatusMessage success={status.success}>
            {status.message}
          </StatusMessage>
        )}
      </Content>
    </Wrapper>
  );
}

export default MainPage; 